
"use client";

import * as React from "react";
import { PageHeader } from "@/components/page-header";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Switch } from "@/components/ui/switch";
import { SegmentedControl, SegmentedControlItem } from "@/components/ui/segmented-control";
import { useToast } from "@/hooks/use-toast";
import { cn } from "@/lib/utils";

const themes = [
    { name: "light", label: "Light" },
    { name: "stone", label: "Stone" },
    { name: "orange", label: "Orange" },
    { name: "green", label: "Green" },
];

export default function SettingsPage() {
  const { toast } = useToast();
  const [name, setName] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [isMounted, setIsMounted] = React.useState(false);
  const [selectedTheme, setSelectedTheme] = React.useState("light");
  const [isDarkMode, setIsDarkMode] = React.useState(false);

  React.useEffect(() => {
    setIsMounted(true);
    const savedTheme = localStorage.getItem("theme") || 'light';
    const savedIsDark = localStorage.getItem("isDarkMode") === 'true';
    
    setSelectedTheme(savedTheme);
    setIsDarkMode(savedIsDark);

    setName(localStorage.getItem("profileName") || "Manager");
    setEmail(localStorage.getItem("profileEmail") || "manager@example.com");
  }, []);
  
  const applyTheme = (theme: string, isDark: boolean) => {
    localStorage.setItem("theme", theme);
    localStorage.setItem("isDarkMode", String(isDark));
    
    // Manually dispatch storage events to notify other tabs/components like the layout
    window.dispatchEvent(new StorageEvent('storage', { key: 'theme', newValue: theme }));
    window.dispatchEvent(new StorageEvent('storage', { key: 'isDarkMode', newValue: String(isDark) }));
  }

  const handleThemeChange = (newTheme: string) => {
    setSelectedTheme(newTheme);
    applyTheme(newTheme, isDarkMode);
  };

  const handleDarkModeToggle = (checked: boolean) => {
    setIsDarkMode(checked);
    applyTheme(selectedTheme, checked);
  };

  const handleProfileSave = () => {
    localStorage.setItem("profileName", name);
    localStorage.setItem("profileEmail", email);
    toast({
      title: "Profile Updated",
      description: "Your profile information has been saved successfully.",
    });
     // Manually dispatch storage events
    window.dispatchEvent(new StorageEvent('storage', { key: 'profileName', newValue: name }));
    window.dispatchEvent(new StorageEvent('storage', { key: 'profileEmail', newValue: email }));
  };

  if (!isMounted) {
    return null; // Render nothing until the client-side logic has run
  }

  return (
    <>
      <PageHeader
        title="Settings"
        description="Manage your account and application settings."
      />
      <div className="grid gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Profile</CardTitle>
            <CardDescription>Update your personal information.</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="name">Name</Label>
              <Input 
                id="name" 
                value={name} 
                onChange={(e) => setName(e.target.value)}
                placeholder="Your Name"
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="email">Email</Label>
              <Input
                id="email"
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="your.email@example.com"
              />
            </div>
          </CardContent>
          <CardFooter className="border-t px-6 py-4">
            <Button onClick={handleProfileSave}>Save</Button>
          </CardFooter>
        </Card>
        <Card>
          <CardHeader>
            <CardTitle>Appearance</CardTitle>
            <CardDescription>
              Customize the look and feel of the application.
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            <div>
              <Label className="font-semibold">Color Theme</Label>
                <SegmentedControl
                    value={selectedTheme}
                    onValueChange={handleThemeChange}
                    className="mt-2"
                >
                    {themes.map((theme) => (
                        <SegmentedControlItem key={theme.name} value={theme.name}>
                            {theme.label}
                        </SegmentedControlItem>
                    ))}
                </SegmentedControl>
            </div>

            <div className="flex items-center justify-between rounded-lg border p-4">
              <Label htmlFor="dark-mode" className="flex flex-col space-y-1">
                <span>Dark Mode</span>
                <span className="font-normal leading-snug text-muted-foreground">
                  Enable for all themes.
                </span>
              </Label>
              <Switch
                id="dark-mode"
                aria-label="Toggle dark mode"
                checked={isDarkMode}
                onCheckedChange={handleDarkModeToggle}
              />
            </div>
          </CardContent>
        </Card>
      </div>
    </>
  );
}